.modal-container {
  display: flex;
  flex-direction: column;
  min-height: 0;
  max-height: 85vh;
  background: #fff;
}

.modal-header {
  padding: 16px 24px;
  border-bottom: 1px solid #e5e7eb;
  flex-shrink: 0;

  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    h2 {
      margin: 0;
      font-size: 20px;
      font-weight: 600;
      color: #111827;
    }
  }

  .subtitle {
    margin: 8px 0 0;
    color: #6b7280;
    font-size: 14px;
  }

  .close-button {
    background: none;
    border: none;
    padding: 8px;
    cursor: pointer;
    color: #6b7280;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      color: #111827;
    }

    ion-icon {
      font-size: 24px;
    }
  }
}

.modal-content {
  flex: 1;
  min-height: 0;
  overflow-y: auto;
  
  form {
    height: 100%;
  }
}

.form-section {
  padding: 24px;
  max-height: calc(85vh - 140px);
}

.form-group {
  position: relative;
  margin-bottom: 20px;

  label {
    display: block;
    margin-bottom: 8px;
    font-size: 14px;
    font-weight: 500;
    color: #374151;

    .required {
      color: #dc2626;
    }
  }
}

.form-control {
  position: relative;
  width: 100%;
  padding: 10px 12px;
  background-color: #FAFAFB;
  border: 1px solid transparent;
  border-radius: 6px;
  font-size: 14px;
  color: #111827;
  transition: border-color 0.2s;

  &:focus {
    outline: none;
    border-color: #2563eb;
  }

  &::placeholder {
    color: #9ca3af;
  }
}

textarea.form-control {
  resize: vertical;
  min-height: 80px;
}

.character-count {
  position: absolute;
  bottom: 13px;
  right: 13px;
  font-size: 12px;
  color: #6b7280;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  color: #374151;
  margin-bottom: 4px;

  .form-checkbox {
    width: 16px;
    height: 16px;
    accent-color: #4F46E5;
    cursor: pointer;
  }

  .checkmark {
    display: none;
  }
}

.form-help {
  display: block;
  font-size: 12px;
  color: #6b7280;
  margin-top: 4px;
}

.form-row {
  display: flex;
  gap: 16px;
  margin-bottom: 20px;

  .form-group {
    margin-bottom: 0;
    flex: 1;
  }
}

.modal-footer {
  padding: 16px 24px;
  border-top: 1px solid #e5e7eb;
  background-color: #fff;
  flex-shrink: 0;

  .button-group {
    display: flex;
    gap: 12px;
    justify-content: flex-end;

    .btn {
      padding: 8px 16px;
      border-radius: 6px;
      font-size: 14px;
      font-weight: 500;
      display: flex;
      align-items: center;
      gap: 8px;
      cursor: pointer;
      transition: all 0.2s;

      &:disabled {
        opacity: 0.5;
        cursor: not-allowed;
      }
    }

    .btn-outline {
      background: none;
      border: 1px solid #d1d5db;
      color: #374151;

      &:hover:not(:disabled) {
        background-color: #f8fafc;
        border-color: #9ca3af;
      }
    }

    .btn-primary {
      background-color: #4F46E5;
      border: 1px solid #4F46E5;
      color: #fff;

      &:hover:not(:disabled) {
        background-color: #4338ca;
        border-color: #4338ca;
      }

      &:disabled {
        background-color: #9ca3af;
        border-color: #9ca3af;
        cursor: not-allowed;
        opacity: 0.6;
      }
    }
  }
}

@media (max-width: 768px) {
  .modal-container {
    max-height: none;
    height: 100vh;
  }

  .modal-body {
    overflow-y: auto;
  }

  .modal-content {
    height: auto;
    overflow: auto;
  }

  .form-control {
    font-size: 16px;
    padding: 12px;
  }

  textarea, select, input {
    font-size: 14px!important;
    padding: 12px;
  }

  .input-with-button {
    flex-direction: column;
    
    .add-button {
      position: static;
      margin-top: 8px;
      width: 100%;
      justify-content: center;
    }
  }
}

// Configuración específica para PWA standalone en móviles
@media all and (display-mode: standalone) {
  @media (max-width: 768px) {
    .modal-container {
      height: calc(100vh - var(--safe-area-top, 0px) - var(--safe-area-bottom, 0px));
      max-height: calc(100vh - var(--safe-area-top, 0px) - var(--safe-area-bottom, 0px));
    }

    .modal-header {
      padding-top: calc(16px + var(--safe-area-top, 0px));
    }

    .modal-footer {
      padding-bottom: calc(16px + var(--safe-area-bottom, 0px));
    }
  }
}

// Configuración específica para iOS en PWA standalone
@supports (-webkit-touch-callout: none) {
  @media all and (display-mode: standalone) {
    @media (max-width: 768px) {
      .modal-container {
        height: calc(100vh - var(--safe-area-top, 0px) - var(--safe-area-bottom, 0px));
        max-height: calc(100vh - var(--safe-area-top, 0px) - var(--safe-area-bottom, 0px));
      }

      .modal-header {
        padding-top: calc(16px + var(--safe-area-top, 0px));
      }

      .modal-footer {
        padding-bottom: calc(16px + var(--safe-area-bottom, 0px));
      }
    }
  }
}

// Fallback para navegadores que no soportan env()
@supports not (padding-top: env(safe-area-inset-top)) {
  @media all and (display-mode: standalone) {
    @media (max-width: 768px) {
      .modal-container {
        height: calc(100vh - 40px);
        max-height: calc(100vh - 40px);
      }

      .modal-header {
        padding-top: 36px;
      }

      .modal-footer {
        padding-bottom: 36px;
      }
    }
  }
}